import React, { useEffect, useState } from "react"
import styled from "styled-components"
import { PrimaryCard } from "../../components/Card"
import Countdown from "react-countdown";
import { ButtonPrimary } from "../../components/Button";
import { useWeb3React } from "@web3-react/core";
import { MarketGenerationService } from "../../services/MarketGenerationService";
import ContentLoader from "react-content-loader";
import { CountdownRenderer } from "../../components/CountdownRenderer";
import { Claim } from "../../components/Claim";
import { Contribute } from "../../components/Contribute";
import { generationEndDate } from "../../constants";
import WalletModal from '../../components/WalletModal'
import { MarketDistributionService } from "../../services/MarketDistributionService";

const PageWrapper = styled.div`
    padding-top:2em;

 ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-top:0em;
 `};
`

const CardContent = styled.div`
    display: grid;
    grid-gap: 1.5em;
    padding-bottom: 0.5em;
    width: 26em;


 ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    width: 100%;
    font-size: 0.75em;   
 `};
`

const Title = styled.div`
    font-family: Arial;
    padding: 0.5em 0;
    font-size: 2em;
    letter-spacing:0.025em;
    text-align: center
`

const Subtitle = styled.div`
    padding: 0.25em;
    font-size: 0.875rem;
    text-align: center;    
`

const Text = styled.div`
    font-size: 0.875rem;
    text-align: center;
    justify-self: center;
    color: ${({ theme }) => theme.text3};
`

const GreenText = styled.span`
    font-size: 1.25em;
    color: ${({ theme }) => theme.primary1};
`

const Contribution = styled.div`
    font-size: 1rem;    
    text-align: center
`

const ContributionInfo = styled.div`
    display: grid;
    grid-gap: 1em;
    padding: 0.5em 0;
`

const ConnectWalletWrapper = styled.div`
    padding-top: 2em;
`

const ContributionGrid = styled.div`
    display: grid;
    grid-template-columns: 9rem 6rem 3rem;
    justify-items:end;
    color: ${({ theme }) => theme.text3};
    grid-gap:0.5em;
    justify-self:center;
`

const ContributionLoader = () => (
    <ContentLoader
        width={110}
        height={20}
        speed={1}
        animate={true}
        backgroundColor="#f6f6ef"
        foregroundColor="#e8e8e3"
        backgroundOpacity={0.06}
        foregroundOpacity={0.12}
    >
        <rect x="0" y="0" width="110" height="20" />
    </ContentLoader>)

export const MarketGenerationEvent = () => {
    const { account, library, chainId } = useWeb3React()
    const [contribution, setContribution] = useState<string>("")
    const [contributionLoading, setContributionLoading] = useState<boolean>(false)
    const [totalContribution, setTotalContribution] = useState<string>("")
    const [cotributionPerRound, setContributionPerRound] = useState<string[]>(["", "", ""])
    const [totalContributionLoading, setTotalContributionLoading] = useState<boolean>(false)
    const [cotributionPerRoundLoading, setCotributionPerRoundLoading] = useState<boolean>(false)
    const [distributionComplete, setDistributionComplete] = useState<boolean>(false)
    const [modalOpen, setModalOpen] = useState<boolean>(false)
    const toggleWalletModal = () => { setModalOpen(!modalOpen) }
  
    const getContributionStatus = async () => {
        if (library && account && chainId) {

            const distributionService = new MarketDistributionService(library, account!)
            setDistributionComplete(await distributionService.distributionComplete())

            const service = new MarketGenerationService(library, account!)
            setContributionLoading(true)
            setContribution(await service.getContribution(account))
            setContributionLoading(false)

            setCotributionPerRoundLoading(true)
            setContributionPerRound(await service.getContributionPerRound())
            setCotributionPerRoundLoading(false)

            setTotalContributionLoading(true)
            setTotalContribution(await service.getTotalContribution())
            setTotalContributionLoading(false)
        }
    }

    useEffect(() => {
        getContributionStatus()
        const timer = setInterval(() => getContributionStatus(), 30000)
        return () => clearInterval(timer)
    }, [library, account, chainId])

   
    return (
        <PageWrapper>
            <PrimaryCard width="auto">
                <CardContent>
                    <div>
                        <Title>Market Generation Event</Title>
                        <Text>ends in</Text>
                    </div>                    
                    <Countdown date={generationEndDate} renderer={CountdownRenderer} />
                    <Text>or when hard cap of <GreenText>1,234,567.89 USDT</GreenText> reached</Text>
                    {account
                        ?
                        <>
                            <ContributionInfo>
                                <ContributionGrid>
                                    <Subtitle>Total contribution</Subtitle>
                                    {totalContributionLoading ? <><ContributionLoader /><Contribution>USDT</Contribution></> : <><Contribution>{totalContribution}</Contribution><Contribution>USDT</Contribution></>}
                                    <Subtitle>1 Round</Subtitle>
                                    {cotributionPerRoundLoading ? <><ContributionLoader /><Contribution>USDT</Contribution></>  : <><Contribution>{cotributionPerRound[0]}</Contribution><Contribution>USDT</Contribution></>}
                                    <Subtitle>2 Round</Subtitle>
                                    {cotributionPerRoundLoading ?<><ContributionLoader /><Contribution>USDT</Contribution></>  : <><Contribution>{cotributionPerRound[1]}</Contribution><Contribution>USDT</Contribution></>}
                                    <Subtitle>3 Round</Subtitle>
                                    {cotributionPerRoundLoading ? <><ContributionLoader /><Contribution>USDT</Contribution></> : <><Contribution>{cotributionPerRound[2]}</Contribution><Contribution>USDT</Contribution></>}
                                    <Subtitle>My contribution</Subtitle>
                                    {contributionLoading ? <><ContributionLoader /><Contribution>USDT</Contribution></> : <><Contribution>{contribution} </Contribution><Contribution>USDT</Contribution></>}
                                </ContributionGrid>
                               
                            </ContributionInfo>
                            
                            {distributionComplete
                                ? <Claim />
                                : <Contribute onContribute={getContributionStatus}/> 
                            }
                        </>
                        :
                        <>
                        <WalletModal walletModalOpen={modalOpen} toggleWalletModal={toggleWalletModal} />
                        <ConnectWalletWrapper>
                            <ButtonPrimary onClick={toggleWalletModal}>Connect to a wallet</ButtonPrimary>
                        </ConnectWalletWrapper>
                        </>}
                       
                </CardContent>
            </PrimaryCard>
        </PageWrapper>
    )
}